/*--- NAVBAR SECTION ---*/
.navbar { 
    background-color: #ECF5FF !important;
}

.navbar-brand {
    font-size: 28px;
    font-weight: 700;
    line-height: 33px;
    letter-spacing: 0.1mm;
}

.navbar-brand img {
    height: 36px;
    margin-right: 5px;
    margin-bottom: 4px;
}

.navbar-light .navbar-nav .nav-link {
    color: #080808;
}
/*--- END NAVBAR SECTION ---*/

/*--- HERO SECTION ---*/
.hero {
    padding-top: 4.5rem;
    padding-bottom: 10.5rem;
    padding-left: 0;
    padding-right: 0px;
    background: url(/epipay/static/media/hero_background.2c78b57f.svg) no-repeat;
    background-size: cover;
    background-position: bottom left;
}

.hero .hero-title{
    font-size: 28px;
}

.hero .google-btn {
    padding: 2rem 0;
}

.hero .google-btn img {
    height: 60px;
}
/* --- END HERO SECTION ---*/

/* --- Last Claims --- */
.last-claims {
    padding: 3.5rem 0;
}

.card-body .title {
    padding: 10px 0px;
    border-bottom: 2px solid #ebedec;
}

.card-body .title .text-title{
    font-size: 1.5em;
    color: #717787;
}

.card-body .price {
    padding: 15px 0px;
    border-bottom: 2px solid #ebedec;
}

.card-body .price .nom-price {
    font-size: 3em;
    color: #717787;
    line-height: 0.7;
}

.price span:first-of-type{
    font-size: 0.35em;
    vertical-align: top;
    letter-spacing: 0.15em;
    @media screen and (max-width: 1068px){
        font-size: 0.3em;
    }
}

.price span:last-of-type{
    vertical-align: bottom;
    font-size: 0.30em;
    letter-spacing: 0.04em;
    padding-left: 0.2em;
    @media screen and (max-width: 1068px){
        font-size: 0.25em;
    }
}

.card-body .item {
    padding-top: 10px;
    border-bottom: 2px solid #ebedec;
    text-align: left;
    color: #717787;
}

.item ul {
    list-style: none;
    font-size: 0.9em;
}

.item li:before {
    content: 'âœ“';
    display: inline-block;
    margin-right: 15px;
    color: white;
    background-color: #FEC93D;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    font-size: 0.8em;
    padding: 2px;
    text-align: center;
}

.card-body .btn-claim {
    padding-top: 15px
}

