@import url('https://fonts.googleapis.com/css?family=Playfair+Display&display=swap');

body {
    background: url('./images/hero_background.svg') no-repeat;
    height: 600px;
    background-position: center;
    position: relative;
    background-size: cover;
}

h1,h2,h3,h4,h5,h6 {
    font-weight: bold;
}

.hubungi {
    font-weight: 400;
}

.heading-line{
    width: 45px;
    height: 4px;
    background-color: #FEC93D;
    border-radius: 1rem;
}

/*--- NAVBAR SECTION ---*/
.navbar { 
    background-color: #ECF5FF !important;
}

.navbar-brand {
    font-size: 28px;
    font-weight: 700;
    line-height: 33px;
    letter-spacing: 0.1mm;
}

.navbar-brand img {
    height: 36px;
    margin-right: 5px;
    margin-bottom: 4px;
}

.navbar-light .navbar-nav .nav-link {
    color: #080808;
}
/*--- END NAVBAR SECTION ---*/

/* TITLE*/
.title {
    padding: 3rem 0;
}
/* END TITLE*/

/* TITLE*/
.video {
    padding: 0rem;
}
/* END TITLE*/

/* Corporate Influencer */
.corporate-influencer {
    padding: 3.5rem 0rem 1rem 0rem;
}
.via-whatsapp img {
    width: 60%;
}

.apps-android img {
    width: 50%;
}
/* End Corporate Influencer */

/* Cara Penggunaan */
.cara-penggunaan {
    padding: 1rem 0rem 2rem 0rem;
}
/* End Cara Penggunaan */

/* hubungi-kami */
.hubungi-kami {
    padding: 2rem 0rem 4rem 0rem;
}
/* End hubungi-kami */

/*--- Footer ---*/
.footer {
    padding-top: 2rem;
    padding-bottom: 2rem;
    padding-left: 0;
    padding-right: 0;
}

.footer ul {
    padding-inline-start: 26px;
    list-style: none;
}

.footer ul li {
    display: inline;
    padding-right: 1rem;
}

.footer ul li a {
    color: #080808;
    font-weight: 600;
    font-size: .90rem;
}

.footer .line-footer {
    border-top: 1px solid #D4D4D4;
}

.footer p {
    font-size: .75rem;
}
/*--- End Footer ---*/

@media (min-width: 768px) {
    .navbar { 
        padding: 2.3rem 0;
    }
    
    .navbar-nav li {
        font-size: 1.1em;
    }

    .navbar-expand-lg .navbar-nav .nav-link {
        padding-right: .4rem;
        padding-left: 3rem;
    }

    .btn-daftar{
        padding-left: 2rem;
        padding-right: 2rem;
        margin-left: 4rem;
    }

    .navbar-light .navbar-nav .nav-link img {
        margin-top: -5px;
        height: 60px;
    }

    /* * Modal Panduan */
    .modal-panduan .modal-sm {
        max-width: 350px;
    }

    .video .row {
        background: url('./images/behind_video_kiri.png') no-repeat, url('./images/behind_video_kanan.png') no-repeat;
        background-size: 
        100px 150px,
        100px 100px;
        background-position:
        left bottom 30px,
        right 30px bottom 30px;
        
    }

    .via-whatsapp img {
        width: 20%;
    }

    .apps-android img {
        width: 15%;
    }

}

@media (min-width: 320px) {
    .navbar-light .navbar-nav .nav-link img {
        height: 40px;
    }
}