@import url('https://fonts.googleapis.com/css?family=Playfair+Display&display=swap');

body {
    background: url('./images/hero_background.svg') no-repeat;
    height: 600px;
    background-position: center;
    position: relative;
    background-size: cover;
}

h1, h5 {
    font-weight: bold;
}

/*--- NAVBAR SECTION ---*/
.navbar { 
    background-color: #ECF5FF !important;
}

.navbar-brand {
    font-size: 28px;
    font-weight: 700;
    line-height: 33px;
    letter-spacing: 0.1mm;
}

.navbar-brand img {
    height: 36px;
    margin-right: 5px;
    margin-bottom: 4px;
}

.navbar-light .navbar-nav .nav-link {
    color: #080808;
}
/*--- END NAVBAR SECTION ---*/

/* TITLE*/
.title-panduan {
    padding: 4rem 0;
}
/* END TITLE*/

/* MENU UTAMA*/
.menu-utama img {
    height: 75px;
}
/* END MENU UTAMA*/

/* Payment & Lav */
.menu-payment-law{
    padding: 3.5rem 0;
}
/* End Payment & Lav */

/*--- Footer ---*/
.footer {
    /* padding: 3.5rem 0; */
    padding-top: 5rem;
    padding-bottom: 2rem;
    padding-left: 0;
    padding-right: 0;
}

.footer ul {
    padding-inline-start: 26px;
    list-style: none;
}

.footer ul li {
    display: inline;
    padding-right: 1rem;
}

.footer ul li a {
    color: #080808;
    font-weight: 600;
    font-size: .90rem;
}

.footer .line-footer {
    /* background-color: #090913; */
    border-top: 1px solid #D4D4D4;
}

.footer p {
    font-size: .75rem;
}
/*--- End Footer ---*/

/* Modal Panduan */
.modal .modal-sm {
    max-width: 350px;
}

.modal-content .modal-header{
    background-color: #ECF5FF !important;
    border-bottom: none;
}

.modal-content .modal-footer{
    background-color: #ECF5FF !important;
    border-top: none;
}
/* End Modal Panduan */

@media (min-width: 768px) {
    .navbar { 
        padding: 2.3rem 0;
    }
    
    .navbar-nav li {
        font-size: 1.1em;
    }

    .navbar-expand-lg .navbar-nav .nav-link {
        padding-right: .4rem;
        padding-left: 3rem;
    }

    .btn-daftar{
        padding-left: 2rem;
        padding-right: 2rem;
        margin-left: 4rem;
    }

    .navbar-light .navbar-nav .nav-link img {
        margin-top: -5px;
        height: 60px;
    }

    /* * Modal Panduan */
    .modal-panduan .modal-sm {
        max-width: 350px;
    }
}

@media (min-width: 320px) {
    .navbar-light .navbar-nav .nav-link img {
        height: 40px;
    }
}