@import url('https://fonts.googleapis.com/css?family=Playfair+Display&display=swap');

body {
    background: url('./images/bg-left.svg') no-repeat, url('./images/bg-right.svg') no-repeat;
    background-position:
    left top 600px,
    right top 1500px;
    background-size: 
    40% 40%,
    40% 40%;
}

h1, h2, h5 {
    font-weight: bold;
}

.heading-line{
    width: 45px;
    height: 4px;
    background-color: #FEC93D;
    border-radius: 1rem;
}

/*--- NAVBAR SECTION ---*/
.navbar { 
    background-color: #ECF5FF !important;
}

.navbar-brand {
    font-size: 28px;
    font-weight: 700;
    line-height: 33px;
    letter-spacing: 0.1mm;
}

.navbar-brand img {
    height: 36px;
    margin-right: 5px;
    margin-bottom: 4px;
}

.navbar-light .navbar-nav .nav-link {
    color: #080808;
}
/*--- END NAVBAR SECTION ---*/

/*--- HERO SECTION ---*/
.hero {
    padding-top: 4.5rem;
    padding-bottom: 10.5rem;
    padding-left: 0;
    padding-right: 0px;
    background: url('./images/hero_background.svg') no-repeat;
    background-size: cover;
    background-position: bottom left;
}

.hero .hero-title{
    font-size: 28px;
}

.hero .google-btn {
    padding: 2rem 0;
}

.hero .google-btn img {
    height: 60px;
}
/* --- END HERO SECTION ---*/

/* --- Last Claims --- */
.last-claims {
    padding: 3.5rem 0;
}

.card-body .title {
    padding: 10px 0px;
    border-bottom: 2px solid #ebedec;
}

.card-body .title .text-title{
    font-size: 1.5em;
    color: #717787;
}

.card-body .price {
    padding: 15px 0px;
    border-bottom: 2px solid #ebedec;
}

.card-body .price .nom-price {
    font-size: 3em;
    color: #717787;
    line-height: 0.7;
}

.price span:first-of-type{
    font-size: 0.35em;
    vertical-align: top;
    letter-spacing: 0.15em;
    @media screen and (max-width: 1068px){
        font-size: 0.3em;
    }
}

.price span:last-of-type{
    vertical-align: bottom;
    font-size: 0.30em;
    letter-spacing: 0.04em;
    padding-left: 0.2em;
    @media screen and (max-width: 1068px){
        font-size: 0.25em;
    }
}

.card-body .item {
    padding-top: 10px;
    border-bottom: 2px solid #ebedec;
    text-align: left;
    color: #717787;
}

.item ul {
    list-style: none;
    font-size: 0.9em;
}

.item li:before {
    content: 'âœ“';
    display: inline-block;
    margin-right: 15px;
    color: white;
    background-color: #FEC93D;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    font-size: 0.8em;
    padding: 2px;
    text-align: center;
}

.card-body .btn-claim {
    padding-top: 15px
}

/* --- END Last Claims --- */


/*--- LAYANAN SECTION ---*/
.pelayanan {
    padding: 3.5rem 0;
}
/* --- END LAYANAN SECTION --- */

/*--- LAYANAN FINANCIAL SECTION ---*/
.layanan-financial {
    padding: 3.5rem 0;
}

.layanan-financial ul{
    padding-inline-start: 26px;
    list-style-image: url('./images/circle.png');
}

.layanan-financial ul li {
    font-size: 1rem;
    font-weight: 600;
    padding-left: 1rem;
    padding-bottom: 2rem;
}
/*--- END LAYANAN FINANCIAL SECTION ---*/

/*--- TRANSFER FUN ---*/
.transfer-fund {
    padding: 3.5rem 0;
}

.transfer-fund ul{
    padding-inline-start: 26px;
    list-style-image: url('./images/circle.png');
}

.transfer-fund ul li {
    font-size: 1rem;
    font-weight: 600;
    padding-left: 1rem;
    padding-bottom: 2rem;
}
/*--- END TRANSFER FUN ---*/

/*--- HUKUM EXPRESS ---*/
.hukum-express {
    padding: 3.5rem 0;
}

.hukum-express ul{
    padding-inline-start: 26px;
    list-style-image: url('./images/circle.png');
}

.hukum-express ul li {
    font-size: 1rem;
    font-weight: 600;
    padding-left: 1rem;
    padding-bottom: 2rem;
}
/*--- END HUKUM EXPRESS ---*/

/*--- INFLUENCER ---*/
.influencer {
    padding: 3.5rem 0;
}

.influencer ul{
    padding-inline-start: 26px;
    list-style-image: url('./images/circle.png');
}

.influencer ul li {
    font-size: 1rem;
    font-weight: 600;
    padding-left: 1rem;
    padding-bottom: 2rem;
}
/*--- END INFLUENCER ---*/

/*--- Video ---*/
.video {
    padding: 3.5rem 0;
}
/*--- End Video ---*/

/*--- E Payment ---*/
.e-payment {
    padding: 3.5rem 0;
}

.e-payment .box-img-fitur{
    padding: 0px;
}

.e-payment .title-fitur {
    font-size: 0.80rem;
    font-weight: 600;
}
/*--- E Payment ---*/

/*--- Bukopin ---*/
.bukopin {
    padding-top: 2rem;
    padding-bottom: 3.5rem;
    padding-left: 0;
    padding-right: 0;
}
/*--- End Bukopin ---*/

/*--- Footer ---*/
.footer {
    padding-top: 3.5rem;
    padding-bottom: 2rem;
    padding-left: 0;
    padding-right: 0;
}

.footer ul {
    padding-inline-start: 26px;
    list-style: none;
}

.footer ul li {
    display: inline;
    padding-right: 1rem;
}

.footer ul li a {
    color: #080808;
    font-weight: 600;
    font-size: .90rem;
}

.footer .line-footer {
    border-top: 1px solid #D4D4D4;
}

.footer p {
    font-size: .75rem;
}
/*--- End Footer ---*/

@media (min-width: 768px) {

    /* .hero-col-title {
        padding: 7.5rem 15px;
    } */

    .navbar { 
        padding: 2.3rem 0;
    }
    
    .navbar-nav li {
        font-size: 1.1em;
    }

    .navbar-expand-lg .navbar-nav .nav-link {
        padding-right: .4rem;
        padding-left: 3rem;
    }

    .navbar-light .navbar-nav .nav-link img {
        margin-top: -5px;
        height: 60px;
    }

    .layanan-financial img {
        height: 350px;
    }

    .transfer-fund img {
        height: 310px;
    }

    .hukum-express img {
        height: 340px;
    }

    .influencer img {
        height: 330px;
    }
  
    .video .row {
        background: url('./images/behind_video_kiri.png') no-repeat, url('./images/behind_video_kanan.png') no-repeat;
        background-size: 
        100px 150px,
        100px 100px;
        background-position:
        left bottom 30px,
        right 30px bottom 30px;
        
    }

    .e-payment .box-text-fitur  {
        padding: 20px 0px;
    }

    .e-payment .title-fitur {
        font-size: 1.2rem;
        font-weight: 600;
    }

}

@media (min-width: 320px) {
    .navbar-light .navbar-nav .nav-link img {
        height: 40px;
    }
}