@import url('https://fonts.googleapis.com/css?family=Playfair+Display&display=swap');

h1, h2, h5 {
    font-weight: bold;
}

.heading-line{
    width: 45px;
    height: 4px;
    background-color: #FEC93D;
    border-radius: 1rem;
}

/*--- NAVBAR SECTION ---*/
.navbar { 
    background-color: #ECF5FF !important;
}

.navbar-brand {
    font-size: 28px;
    font-weight: 700;
    line-height: 33px;
    letter-spacing: 0.1mm;
}

.navbar-brand img {
    height: 36px;
    margin-right: 5px;
    margin-bottom: 4px;
}

.navbar-light .navbar-nav .nav-link {
    color: #080808;
}
/*--- END NAVBAR SECTION ---*/

/*--- HERO SECTION ---*/
.hero-business {
    padding-top: 4.5rem;
    padding-bottom: 10.5rem;
    padding-left: 0;
    padding-right: 0px;
    background: url('./images/hero_background.svg') no-repeat;
    background-size: cover;
    background-position: bottom left;
}

.hero-business a {
    padding: 0.575rem 7rem;
}

.hero-business .hero-title {
    font-weight: 600;
}

.hero-business .hero-desc{
    font-size: 25px;
}

.hero-business .google-btn {
    padding: 2rem 0;
}

.hero-business .google-btn img {
    height: 60px;
}
/* --- END HERO SECTION ---*/

/*--- influencerBusiness ---*/
.influencerBusiness {
    padding: 3.5rem 0;
}

.influencerBusiness ul{
    padding-inline-start: 26px;
    list-style-image: url('./images/circle.png');
}

.influencerBusiness ul li {
    font-size: 1rem;
    font-weight: 600;
    padding-left: 1rem;
    padding-bottom: 2rem;
}

.influencerBusiness p {
    line-height: 3rem
}
/*--- END influencerBusiness ---*/

/*--- influencerVideo ---*/
.influencerVideo {
    padding: 3.5rem 0;
}
/*--- End influencerVideo ---*/

/*--- Video ---*/
.video {
    padding: 3.5rem 0;
    /* border: 1px solid black; */
}
/*--- End Video ---*/

/*--- Footer ---*/
.footer {
    padding-top: 3.5rem;
    padding-bottom: 2rem;
    padding-left: 0;
    padding-right: 0;
}

.footer ul {
    padding-inline-start: 26px;
    list-style: none;
}

.footer ul li {
    display: inline;
    padding-right: 1rem;
}

.footer ul li a {
    color: #4b4b50;
    font-weight: 600;
    font-size: .90rem;
}

.footer .line-footer {
    /* background-color: #090913; */
    border-top: 1px solid #D4D4D4;
}

.footer p {
    font-size: .75rem;
}

/*--- End Footer ---*/

@media (min-width: 768px) {
    .navbar { 
        padding: 2.3rem 0;
    }
    
    .navbar-nav li {
        font-size: 1.1em;
    }

    .navbar-expand-lg .navbar-nav .nav-link {
        padding-right: .4rem;
        padding-left: 3rem;
    }

    .navbar-light .navbar-nav .nav-link img {
        margin-top: -5px;
        height: 60px;
    }

    .influencerVideo .row {
        background: url('./images/behind_video_kiri.png') no-repeat, url('./images/behind_video_kanan.png') no-repeat;
        background-size: 
        100px 150px,
        100px 100px;
        background-position:
        left bottom 30px,
        right 30px bottom 30px;
    }
}

@media (min-width: 320px) {
    .navbar-light .navbar-nav .nav-link img {
        height: 40px;
    }
}